
import React from 'react'
import { Link,useHistory } from "react-router-dom";
import "./Login.css";
import { useState }  from "react";
import { auth } from "./firebase";


//login
function Login() {
    const history = useHistory();
    const [email, setEmail] = useState(" ");
    const [password, setPassword] = useState ("  " );
    //sign in
    const signIn = e => {
e.preventDefault();
//some fancy firebbase login shiiiy.......
auth
    .signIn(email,password)
    .then(auth => {
    //it has successfully created a new user with email and password
    if (auth) {
        history.push(' / ')
    }
    })
    .catch(error => alert(error.message))

    }
    //register
const register = e => { 
e.preventDefault();

auth
    .createUserWithEmailAndPassword(email,password)
    .then((auth) => {
    //it has successfully created a new user with email and password
    if(auth) {

history.push('/')
    }
    })
    .catch(error => alert(error.message))
//firebase

}

    return (
        <div className="logo">
            <Link to ="/" >
            <img src="https://bestmattressbuys.com/wp-content/uploads/2016/05/Shop-Small.jpg " alt=" "/>
</Link>
        <div className="login__container"> 
            <h1> Sign-in  </h1>
            <form >
                <h5>E-mail</h5>
                <input type="text" value={email} onChange={e => setEmail(e.target.value)} />
                <h5> Password</h5>
                <input type ="password" value={password} onChange={e => setPassword(e.target.value)} />

                <button type="submit" onClick={signIn}
                className= "login__signInbutton ">Sign In </button>

            </form>
            <p>    
        By signing-in you agree to the  shop's t's and c's.Please see our Privacy Notice,our Cookies Notice and our Interest-Based Ads.
        </p>
<button onClick={register}
className="login__registerButton "> Create your Account 

</button>

</div>

        
        </div>
    )
}

export default Login;
